function listAdd() {
	let n = 0
	let m = 0
	let flag = 1
	let list10 = []
	let special = list8[Math.floor(Math.random()*8 + 1) - 1]
	while(n <= 800){
		if(n == 0) {
			list10.push(special)
			special = list8[Math.floor(Math.random()*8 + 1) - 1]
		}
		else {
			for(let vol of list10) {
				if(vol.teaName != special.teaName ){
					m++
				}
				else {
					continue
				}

			}
			if(m == list10.length) {
				list10.push(special)
				m = 0
				special = list8[Math.floor(Math.random()*8 + 1) - 1]
			}
		}
		n++
	}
	return list10
}

